.contactForm {
    padding: 36px 12px;
    width: 100%;
    background: #eaedf1;

    @media screen and (min-width: 768px) {
        display: flex;
        flex-basis: calc((100% - 24px) / 2);
        padding: 36px 24px;
        flex-grow: 1;
    }

    @media screen and (min-width: 1280px) {
        padding: 48px;
        flex-basis: calc((100% - 48px) / 2);
    }
}

.fieldForm {
    width: 100%;
}

.fieldForm_fieldBlock {
    position: relative;
    padding-bottom: 8px;
    margin-bottom: 28px;
    border-bottom: 1px solid #97d28b;
}

.fieldForm_fieldBlock label {
    color: #173d33;
    text-align: justify;
    font-family: Fira Sans;
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    letter-spacing: -0.64px;
}

.fieldForm_fieldBlock input {
    margin-top: 8px;
    display: flex;
    align-items: center;
    height: 22px;
    width: 100%;
    border: none;
    background-color: transparent;

    color: #173d33;
    font-family: Fira Sans;
    font-size: 18px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    letter-spacing: -0.72px;

    @media screen and (min-width: 1280px) {
        height: 24px;
        margin-top: 12px;
        font-size: 20px;
        letter-spacing: -0.8px;
    }
}

.fieldForm_fieldBlock input::placeholder {
    color: #bdbdbd;
    font-family: Fira Sans;
    font-size: 18px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    letter-spacing: -0.72px;

    @media screen and (min-width: 1280px) {
        font-size: 20px;
        letter-spacing: -0.8px;
    }
}

.fieldForm_fieldBlock_textarea {
    margin-top: 8px;
    width: 100%;
    border: none;
    background-color: transparent;
    display: flex;
    height: 147px;
    flex-direction: column;
    align-items: flex-start;
    gap: 1px;
    align-self: stretch;
    resize: none;

    color: #173d33;
    font-family: Fira Sans;
    font-size: 18px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    letter-spacing: -0.72px;

    @media screen and (min-width: 1280px) {
        margin-top: 12px;
        height: 149px;
        font-size: 20px;
        letter-spacing: -0.8px;
    }
}

.fieldForm_fieldBlock_textarea::placeholder {
    color: #bdbdbd;
    font-family: Fira Sans;
    font-size: 18px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    letter-spacing: -0.72px;

    @media screen and (min-width: 1280px) {
        font-size: 20px;
        letter-spacing: -0.8px;
    }
}

.fieldForm_fieldBlock_error {
    position: absolute;
    bottom: -26px;
    z-index: 2;
    width: 100%;
    height: 22px;
    padding-top: 8px;

    color: #d28b8b;
    text-align: right;
    font-family: Fira Sans;
    font-size: 12px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    letter-spacing: -0.48px;

    @media screen and (min-width: 1280px) {
        height: 26px;
        padding-top: 12px;
    }
}

.fieldForm_fieldBlock_textareaCounter {
    position: absolute;
    z-index: 9;
    bottom: -36px;
    // right: 8px;

    color: #bdbdbd;
    font-family: Fira Sans;
    font-size: 18px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    letter-spacing: -0.72px;

    @media screen and (min-width: 1280px) {
        font-size: 20px;
        letter-spacing: -0.8px;
        bottom: 8px;
        right: 8px;
    }
}

.fieldForm_fieldBlock.textArea {
    margin-bottom: 16px;

    @media screen and (min-width: 1280px) {
        margin-bottom: 32px;
    }
}

.fieldForm_buttonWrap {
    width: 100%;
    display: flex;
    justify-content: flex-end;
}

.BtnSubmit {
    display: inline-flex;
    height: 39px;
    padding: 4px 4px 4px 16px;
    justify-content: center;
    align-items: center;
    gap: 12px;
    flex-shrink: 0;

    border-radius: 500px;
    border: 1px solid #97d28b;
    transition: background-color 0.3s ease-in-out, border-color 0.3s ease-in-out,
        color 0.3s ease-in-out;

    color: #173d33;
    font-family: Fira Sans;
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    letter-spacing: -0.64px;
}

.BtnSubmit:hover {
    background-color: #173d33;
    border: 1px solid #173d33;
    color: #97d28b;
}

.BtnSubmit_svgWrapper {
    display: flex;
    padding: 8px;
    justify-content: center;
    align-items: center;
    gap: 10px;

    border-radius: 100px;
    background: #97d28b;
}